<template>
  <div>
    <div id="canvas-frame"></div>
    <!-- <div id="info">Description</div> -->
  </div>
</template>

<script>
import * as THREE from "three";
// 
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// 
import { FontLoader } from "three/examples/jsm/loaders/FontLoader.js";
// 
import { TextGeometry } from "three/examples/jsm/geometries/TextGeometry.js";
//
import Animations from "@/assets/utils/animations";
// 
import Stats from "@/assets/js/Stats.js";
//GLTFLoader---3D
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
// import fontPath from "three/examples/fonts/helvetiker_bold.typeface.json";
// import fontPath from "three/examples/fonts/optimer_bold.typeface.json";
import fontPath from "@/assets/fonts/FZShuTi_Regular.json";
import skyTexture from "@/assets/image/sky.jpg";
export default {
  data() {
    return {
      statsObj: null,
      threeObj: {
        scene: null,
        camera: null,
        renderer: null,
        controls: null,
      },
      meshes: [],
      loadingProcessTimeout: null,
      loadingProcess: null,
      circleR: 5
    };
  },
  created() {},
  mounted() {
    let that = this;
    this.initThree();
    this.controlsWebGL();
    this.threeWebGLMain();

    this.statsObj = this.stats();
  },
  methods: {
    // 
    stats() {
      var stats = new Stats();
      stats.setMode(1); // 0: fps, 1: ms
      // stats
      stats.domElement.style.position = "absolute";
      stats.domElement.style.left = "0px";
      stats.domElement.style.top = "0px";
      document.body.appendChild(stats.domElement);
      return stats;
    },
    // 
    controlsWebGL() {
      let { scene, camera, renderer } = this.threeObj;
      // 
      let controls = new OrbitControls(camera, renderer.domElement);
      console.log(controls, "controls---");
      controls.target.set(0, 0, 0);
      controls.enableDamping = true;
      //    
      controls.enablePan = true;
      // 
      controls.enableZoom = true;
      // 
      controls.minPolarAngle = -100.4;
      controls.maxPolarAngle = 100.8;
      // 
      controls.minAzimuthAngle = -100.9;
      controls.maxAzimuthAngle = 100.9;
      this.threeObj = { ...this.threeObj, controls };
    },
    dirLight() {
      let color = 0xffffff;
      let light = new THREE.DirectionalLight(color, 0.8);
      // light.intensity = 0.8;//
      // console.log(light, "light---------");
      //   light.position.set(0, 10, 0);
      light.castShadow = true;
      //   light.target = cube;
      //   light.shadow.mapSize.width = 512 * 12;
      //   light.shadow.mapSize.height = 512 * 12;
      // light.shadow.camera.top = 400;
      //   light.shadow.camera.bottom = -40;
      //   light.shadow.camera.left = -400;
      //   light.shadow.camera.right = 40;
      return light;
    },
    // 
    createSphere() {
      // 1:  , 28 /36: 
      let R = 8;
      let longitude = 20; //
      let latitude = 20; //

      var sphereGeometry = new THREE.SphereGeometry(R, longitude, latitude);
      var sphereMaterial = new THREE.MeshBasicMaterial({
        color: 0x7777ff,
        // color: 0x000000,
        opacity: 0.5,
        wireframe: true,
      });
      console.log(sphereMaterial, "sphereMaterial-----");
      sphereMaterial.intensity = 0.5;
      var sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);

      sphere.castShadow = true;
      return sphere;
    },

    // 
    createSphereMore(R, longitude, latitude) {
      // 1:  , 28 /36: 
      var sphereGeometry = new THREE.SphereGeometry(R, longitude, latitude);
      var sphereMaterial = new THREE.MeshBasicMaterial({
        // color: 0x7777ff,
        color: 0xf0f000,
        opacity: 0.5,
        // wireframe: true,
      });
      console.log(sphereMaterial, "sphereMaterial-----");
      sphereMaterial.intensity = 0.5;
      var sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);

      sphere.castShadow = true;
      return sphere;
    },
    // 
    /**
     * @degree 
     * @r 
     * */ 
    circleTrack(degree, r, x0=0, y0=0) {
      var hudu = ((2 * Math.PI) / 360) * degree;
      var x1 = x0 + Math.sin(hudu) * r;
      var y1 = y0 - Math.cos(hudu) * r;
      return {x:x1,y:y1 - 2};
    },
    //   
    sphereMain() {
      let that = this;
      let { scene } = this.threeObj;
      let x0 = 0,
        y0 = 0;
      let sphereArr = [];
      for (let degree = 0; degree < 360; degree += 30) {
        let sphere = this.createSphereMore(1, 20, 20);
        (function (degree, sphere) {
          let {x, y} = that.circleTrack(degree, that.circleR);
          sphere.position.set(x, y, -8);
          // let z = y;
          // sphere.position.set(x, 0, z);
          sphere.degree = degree;
          // scene.add(sphere);
          sphereArr.push(sphere);
        })(degree, sphere);
      }
      return sphereArr;
      // scene.add(sphere);
    },
    // 
    async createFont() {
      function createText(geom) {
        console.log(geom, "geom-----geom");
        geom.computeBoundingBox();
        var materials = [
          // 
          new THREE.MeshBasicMaterial({
            // color: Math.random() * 0xffffff,
            // color: 0xf2f8f7,
            color: 0x000000,
          }),
          //   
          new THREE.MeshBasicMaterial({
            color: 0xffffff,
          }),
        ];
        var mesh = new THREE.Mesh(geom, materials);
        return mesh;
      }
      return new Promise((resolve, reject) => {
        //   const fontPath = request(url);
        let fontLoad = new FontLoader();
        let font = fontLoad.parse(fontPath);
        console.log(font, "font---");
        let talk = "";
        // talk =    `  \n \n  \n `;
        var text = createText(
          new TextGeometry(talk, {
            font: font,
            size: 1, //
            height: 0.2, //
            curveSegments: 2,
            // bevelEnabled: true,//  
            bevelThickness: 0.5,
            bevelSize: 0.01, // border-radius --- 
            bevelSegments: 1,
          })
        );
        resolve(text);
      });
    },
    // 3D
    loadModel3D() {
      let that = this;
      let { scene, camera, controls } = this.threeObj;
      const manager = new THREE.LoadingManager();
      manager.onStart = (url, loaded, total) => {};
      manager.onLoad = () => {
        console.log("Loading complete!");
      };
      manager.onProgress = async (url, loaded, total) => {
        if (Math.floor((loaded / total) * 100) === 100) {
          that.loadingProcessTimeout &&
            clearTimeout(that.loadingProcessTimeout);
          that.loadingProcessTimeout = setTimeout(() => {
            // that.setState({ loadingProcess: Math.floor(loaded / total * 100) });
            that.loadingProcess = Math.floor((loaded / total) * 100);
            Animations.animateCamera(
              camera,
              controls,
              { x: 0, y: -1, z: 20 },
              { x: 0, y: 0, z: 0 },
              3600,
              () => {}
            );
          }, 800);
        } else {
          //   that.setState({ loadingProcess: Math.floor(loaded / total * 100) });
          that.loadingProcess = Math.floor((loaded / total) * 100);
        }
      };
      return new Promise((resolve, reject) => {
        let GLTFload = new GLTFLoader(manager);
        // let model = GLTFload.parse(modelPanda);
        // console.log(model, "model----")
        GLTFload.load(
          "/models/bingdundun.glb", //
          function (mesh) {
            console.log(mesh.scene, "gltf.scene");
            // resolve(gltf.scene);
            // let { meshes } = that;
            mesh.scene.traverse(function (child) {
              if (child.isMesh) {
                console.log(child, "child----");
                that.meshes.push(child);
                if (child.name === "oldtiger001") {
                  child.material.metalness = 0.5;
                  child.material.roughness = 0.8;
                }
                if (child.name === "oldtiger002") {
                  child.material.transparent = true;
                  child.material.opacity = 0.5;
                  child.material.metalness = 0.2;
                  child.material.roughness = 0;
                  child.material.refractionRatio = 1;
                  child.castShadow = true;
                }
              }
            });
            // mesh.scene.rotation.y = Math.PI / 24;
            // mesh.scene.rotation.y = Math.PI / 12;
            // mesh.scene.position.set(-8, -12, 0);
            mesh.scene.position.set(0, -5, 3);
            mesh.scene.scale.set(24, 24, 24);
            // scene.add(mesh.scene);

            resolve(mesh.scene);
            // scene.add(gltf.scene);
          },
          function (error) {
            // console.error(error);
          }
        );
      });
    },
    //   
    initThree() {
      let { scene, camera, renderer } = this.threeObj;
      scene = new THREE.Scene();
      camera = new THREE.PerspectiveCamera(
        95,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      );
      //   camera.position.set(0, 0, 100);
      renderer = new THREE.WebGLRenderer();
      renderer.setSize(window.innerWidth, window.innerHeight);
      scene.background = new THREE.TextureLoader().load(skyTexture);
      //   
      let contain = document.getElementById("canvas-frame");
      contain.appendChild(renderer.domElement);
      this.threeObj = {
        scene,
        camera,
        renderer,
      };
    },
    // 
    threeCube() {
      const geometry = new THREE.BoxGeometry();
      //   const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
      const material = new THREE.MeshNormalMaterial();
      // const material = new THREE.MeshLambertMaterial({
      //   // wireframe: true,
      //   color: 0xf0f000,
      // });

      const cube = new THREE.Mesh(geometry, material);
      cube.position.y = -1;
      return cube;
    },
    // 
    threeLine() {
      const material = new THREE.LineBasicMaterial({ color: 0x0000ff });
      const points = [];
      points.push(new THREE.Vector3(-1, 0, 0));
      points.push(new THREE.Vector3(0, 1, 0));
      points.push(new THREE.Vector3(1, 0, 0));
      const geometry = new THREE.BufferGeometry().setFromPoints(points);
      const line = new THREE.Line(geometry, material);
      return line;
    },
    threeWebGLMain() {
      let that = this;
      // 
      let pos = {
        x: 0,
        y: 0,
        z: 0,
      };
      // 
      let shDegree = 0;
      let { scene, camera, renderer } = this.threeObj;
      //  
      const cube = this.threeCube();
      cube.position.set(pos.x + 4, pos.y + 0.2, pos.z - 5);
      scene.add(cube);
      // 
      let sphere = this.createSphere();
      sphere.position.set(pos.x, pos.y - 4, pos.z - 5);
      // scene.add(sphere);
      // 
      let sphereArr = this.sphereMain();
      sphereArr.forEach((item) => {
        // console.log(item, "degree")
        scene.add(item);
      });
      //  
      this.createFont().then((text) => {
        text.position.set(pos.x - 4, pos.y + 7, pos.z - 5);
        scene.add(text);
      });
      // 3D
      this.loadModel3D().then((model3D) => {
        model3D.position.set(pos.x, pos.y - 10, pos.z - 5);
        scene.add(model3D);
      });
      // #region
      // let lit = 5000;
      // let dirLightArr = [
      //     {x:0, y:0, z:lit},
      //     {x:0, y:0, z:-lit},
      //     {x:0, y:lit, z:0},
      //     {x:0, y:-lit, z:0},
      //     {x:lit, y:0, z:0},
      //     {x:-lit, y:0, z:0},
      // ]
      // // 
      // dirLightArr.forEach((item,index)=>{
      //   var dirLight = this.dirLight();
      //   dirLight.position.set(item.x, item.y, item.z);
      //   // scene.add(dirLight);
      // })
      // #regionend
      // 
      var ambientLight = new THREE.AmbientLight(0xffffff);
      ambientLight.intensity = 1;
      scene.add(ambientLight);
      // 
      camera.position.set(0, 0, 8);
      //   camera.lookAt( 0, 0, 0 );
      //   const line = this.threeLine();
      //   scene.add(line);
      //   camera.position.z = 5;
      console.log(sphere, "sphere-----11")
      // 
      renderer.setAnimationLoop(animation);
      function animation() {
        // 
        cube.rotation.x += 0.01;
        cube.rotation.y += 0.01;
        // 
        sphere.rotation.x += 0.01;
        sphere.rotation.y += 0.01;
        if(shDegree<360){
          shDegree++;
        } else {
          shDegree = 0;
        }
        // 
        sphereArr.forEach(item=>{
          let deg = item.degree + shDegree;
          let {x, y} = that.circleTrack(deg, that.circleR);
          item.position.x = x;
          item.position.y = y;
        })
        renderer.render(scene, camera);
        // 
        that.statsObj.update();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#info {
  position: absolute;
  top: 10px;
  width: 100%;
  text-align: center;
  z-index: 100;
  display: block;
}
</style>